import { useState, useEffect } from "react";
import axios from "axios";
import Modal from "react-modal";
import ListAllOrders from "./ListAllOrders";

const customStyles = {
  // Style for the Modal
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    padding: "0px",
    overflowY: "scroll",
    width: "80%",
    maxHeight: "80%",
  },
  overlay: {
    background: "rgba(0,0,0,0.75)",
    zIndex: "100",
    overflow: "hidden",
  },
};

const OrderModal = ({ order, setOrder }) => {
  const [allorders, SetAllOrders] = useState([]);

  useEffect(() => {
    if (order) getAllOrders();
  }, [order]);

  const getAllOrders = () => {
    axios
      .get(
        `https://n39qrnkqc9.execute-api.eu-west-2.amazonaws.com/dev/order?customer_mobile=9891139385`
      )
      .then((res) => {
        console.log("allDetailss123", res.data);
        SetAllOrders(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div>
      <Modal
        ariaHideApp={false}
        isOpen={order}
        onRequestClose={() => {
          setOrder(false);
        }}
        style={customStyles}
      >
        <div className="px-5 py-4">
          <h1 className="restaurent-name text-center mb-0">All Orders</h1>
          <hr className="my-0" />
          <hr className="mt-0" />
          <div>
            {allorders.map((orderInfo, index) => {
              return (
                <div>
                  <ListAllOrders orderInfo={orderInfo} index={index} />
                  <hr style={{ borderTop: "2px dashed black" }} />
                </div>
              );
            })}
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default OrderModal;
