import { useState, useEffect } from "react";
import axios from "axios";
import OrderModal from "../Orders/OrderModal";
import "./templace.css";

// import hero from "./hero.jpg";
import heroShape from "../../assets/svg/heroShape.svg";
import heroPattern from "../../assets/svg/herobgpattren.svg";

import restaurant1 from "../../assets/images/restaurant1.jpg";
import restaurant2 from "../../assets/images/restaurant2.jpg";
import restaurant3 from "../../assets/images/restaurant3.jpg";
import restaurant4 from "../../assets/images/restaurant4.jpg";
// import logo from "../../assets/logo.png";
import Dropdown from "../../components/Dropdown";

const RestaurentTemplate = () => {
  const [restaurentInfo, setRestaurentInfo] = useState(null);
  const [menuItemsForRes, setMenuItemsForRes] = useState(null);
  const [menu, setMenu] = useState(null);
  const [selectedType, setSelectedType] = useState(null);
  const [order, setOrder] = useState(false);

  const fetchURL =
    // "https://raw.githubusercontent.com/euhidaman/Fake_APIs/main/restaurant_details.json";

    useEffect(() => {
      getResDetails();
      getMenuDetails();
      getMenuItemsForRes();
    }, []);

  const getResDetails = () => {
    axios
      .get(
        "https://n39qrnkqc9.execute-api.eu-west-2.amazonaws.com/dev/restaurant"
      )
      .then((res) => {
        const allDetails = res.data;
        res.data.map((item) => {
          if (item.restaurant_id === document.location.pathname.substring(1, 6))
            setRestaurentInfo(item);
        });
        console.log("allDetails", allDetails);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const getMenuDetails = () => {
    axios
      .get(
        `https://n39qrnkqc9.execute-api.eu-west-2.amazonaws.com/dev/menu?restaurant_id=${document.location.pathname.substring(
          1,
          6
        )}`
      )
      .then((res) => {
        console.log("allDetailss123", res.data);
        setMenu(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const getMenuItemsForRes = () => {
    axios
      .get(
        `https://n39qrnkqc9.execute-api.eu-west-2.amazonaws.com/dev/menuitem?restaurant_id=${document.location.pathname.substring(
          1,
          6
        )}`
      )
      .then((res) => {
        const allDetails = res.data;
        console.log("allDetails", allDetails);
        setMenuItemsForRes(allDetails);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    // Filter change
    if (selectedType != null) {
      axios
        .get(
          `https://n39qrnkqc9.execute-api.eu-west-2.amazonaws.com/dev/menuitem?menu_id=${selectedType.value}`
        )
        .then((res) => {
          // setMenuItemsForRes(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, [selectedType]);

  function setOrderFunction(item) {
    const body = {
      restaurant_id: item.restaurant_id,
      order_amount: item.item_price,
      order_discount: 0,
      order_placed_at: "2022-10-29 18:56:59",
      order_amount_final: item.item_price,
      items:
        item.item_id +
        "," +
        item.item_name +
        "," +
        item.item_price +
        "," +
        item.image_url,
      // items: "IT003,Veg Manchurian Gravy,150,http://awssatraining.s3-website-us-east-1.amazonaws.com/veg_manchurian.jfif; IT002,Veg Fried Rice,120,http://awssatraining.s3-website-us-east-1.amazonaws.com/veg_fried_rice.jfif",
      Type: "Order",
      customer_mobile: "9891139385",
      order_status: "Order Placed",
      // order_id: "OR0001",
      // ID: "A00015",
      menu_id: item.menu_id,
      // NewValue: "",
      customer_name: "Ajay Kumar Madan",
    };

    console.log("body", body);
    setOrder(true);

    axios
      .post(
        `https://n39qrnkqc9.execute-api.eu-west-2.amazonaws.com/dev/order`,
        body
      )
      .then((res) => {
        console.log("res", res);
        // setMenuItemsForRes(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    menuItemsForRes != null &&
    restaurentInfo != null &&
    menu != null && (
      <div>
        <OrderModal order={order} setOrder={setOrder} />
        <main className="container">
          <section className="hero-container">
            <div className="hero-image-container">
              <img src={"https://i.postimg.cc/dD9pw9JC/hero.jpg"} alt="hero" className="hero-image" />
              <img
                src={"https://i.postimg.cc/DSydKqBx/hero-Shape.jpg"}
                alt="hero shape"
                className="hero-image-shape"
              />
              <img
                src={"https://i.postimg.cc/94sGTsVk/herobgpattren.jpg"}
                alt="hero bg pattern"
                className="hero-image-pattern"
              />
            </div>
            <div className="hero-description">
              <h1 className="restaurent-name text-center">
                {restaurentInfo.restaurant_name}
              </h1>
              {/* <p>{restaurentInfo.restaurant_type}</p>
              <p>{restaurentInfo.restaurant_description}</p>
              <p>{restaurentInfo.restaurant_address}</p> */}
              <h1 className="hero-text">
                Order food from your favourite restaurant.
              </h1>

              <div className="video-footage-text mt-3">
                <p>
                  Your food will be prepared safely with an experienced chef,
                  without compromising on the quality and hygiene.
                </p>
              </div>
            </div>
          </section>

          <section className="food-list-container">
            <h1 className="hero-text" style={{ textAlign: "center" }}>
              Explore Our Menu
            </h1>
            <div className="food-list-container-diagonal"></div>
            {menu.length > 0 && (
              <div className="d-flex justify-content-center mb-4 mt-5">
                <div className="">
                  <Dropdown
                    menu={menu}
                    selectedType={selectedType}
                    setSelectedType={setSelectedType}
                  />
                </div>
              </div>
            )}

            <div className="food-menu-card-list row mx-0">
              {menuItemsForRes.map((item, index) => {
                return (
                  (selectedType == null ||
                    (selectedType != null &&
                      selectedType.value == item.menu_id)) && (
                    <div className="col-md-4">
                      <div className="food-menu-card">
                        <div className="image-container-food">
                          <button
                            className="button-primary"
                            onClick={() => setOrderFunction(item)}
                          >
                            Order Now
                          </button>
                          <img
                            src={item.image_url}
                            alt="burger"
                            className="food-menu-card-image"
                          />
                        </div>
                        <div className="food-menu-card-description">
                          <h4 className="food-menu-card-title">
                            {item.item_name}
                          </h4>
                          {/* <h4 className="food-menu-card-title">
                          {"Rs " + item.item_price}
                        </h4> */}
                        </div>
                      </div>
                    </div>
                  )
                );
              })}
              {/* <div className="food-menu-card">
                <div className="image-container-food">
                  <button className="button-primary">Order Now</button>
                  <img src={pizza} alt="pizza" className="food-menu-card-image" />
                </div>
                <div className="food-menu-card-description">
                  <h4 className="food-menu-card-title">Pizza</h4>
                </div>
              </div> */}
              {/* <div className="food-menu-card">
                <div className="image-container-food">
                  <button className="button-primary">Order Now</button>
                  <img
                    src={iceCream}
                    alt="icecream"
                    className="food-menu-card-image"
                  />
                </div>
                <div className="food-menu-card-description">
                  <h4 className="food-menu-card-title">Ice Cream</h4>
                </div>
              </div> */}
            </div>
          </section>
          <section className="restaurant-list-container">
            <div className="restaurant-list-text">
              <h1 className="hero-text">
                Order food from favourite restaurants near you.
              </h1>
            </div>
            <div className="restaurant-list-image-container">
              <div className="restaurant-list-row-1">
                <img
                  src={"https://i.postimg.cc/phRvc6XG/restaurant1.jpg"}
                  alt="resstaurant1"
                  className="restaurant1"
                />
                <img
                  src={"https://i.postimg.cc/dL1sKpRg/restaurant.jpg"}
                  alt="resstaurant2"
                  className="restaurant2"
                />
              </div>
              <div className="restaurant-list-row-2">
                <img
                  src={"https://i.postimg.cc/zLm8KBm1/restaurant3.jpg"}
                  alt="resstaurant3"
                  className="restaurant3"
                />
                <img
                  src={"https://i.postimg.cc/rdScJkcR/restaurant4.jpg"}
                  alt="resstaurant4"
                  className="restaurant4"
                />
              </div>
            </div>
          </section>
          {/* <section className="email-subscribe-container">
          <div>
            <h1 className="hero-text">Stay Updated</h1>
            <div className="email-subscribe-input-field">
              <div>
                <input
                  type="email"
                  placeholder="email@email.com"
                  className="email-input"
                />
              </div>
              <button className="button-primary">Get Updates</button>
            </div>
          </div>
        </section> */}
          <section className="quote-container">
            <div className="quote-image-overlay"></div>
            <h3 className="quote-image-text">
              When a man's stomach is full it makes no difference whether he is
              rich or poor.
            </h3>
          </section>
        </main>
      </div>
    )
  );
};

export default RestaurentTemplate;
